import React from 'react';
import { NodeMenu, EdgeMenu, GroupMenu, MultiMenu, CanvasMenu, ContextMenu } from 'gg-editor';
import MenuItem from './MenuItem';
import './index.less';

const FlowContextMenu = () => {
  return (
    <ContextMenu className="contextMenu">
      <NodeMenu>
        <MenuItem command="copy" text=" " />
        <MenuItem command="delete" text=" " />
      </NodeMenu>
      <EdgeMenu>
        <MenuItem command="delete" text=" "/>
      </EdgeMenu>
      <GroupMenu>
        <MenuItem command="copy" text=" "/>
        <MenuItem command="delete" text=" "/>
        <MenuItem command="unGroup" icon="ungroup" text='' />
      </GroupMenu>
      <MultiMenu>
        <MenuItem command="copy" text=" "/>
        <MenuItem command="paste" text=""/>
        <MenuItem command="addGroup" icon="group" text="" />
        <MenuItem command="delete" text=" "/>
      </MultiMenu>
      <CanvasMenu>
        <MenuItem command="undo" text=""/>
        <MenuItem command="redo" text=""/>
        <MenuItem command="pasteHere" icon="paste" text="" />
      </CanvasMenu>
    </ContextMenu>
  );
};

export default FlowContextMenu;
