import React, {Component}from 'react';
import { Divider} from 'antd';
import { Toolbar, Command } from 'gg-editor';
import IconFont from '../../../../common/IconFont';
import styles from './index.module.less';
import Save from './components/Save/Save';


class FlowToolbar extends Component {
  render() {
    return(
      <div className={styles.canvaHeader}>
        <Toolbar className={styles.toolbar}>
          <Command name="clear">
            <div className={styles.com} title="" >
              <IconFont type={`icon-rerunning-instance`}/><span></span>
            </div>
          </Command>
          <Save />
          <Divider type="vertical" className={styles.divider} />

          <Command name="copy">
            <div className={styles.com} title="" >
              <IconFont type={`icon-copy`}/><span></span>
            </div>
          </Command>
          <Command name="paste">
            <div className={styles.com} title="" >
              <IconFont type={`icon-paste`}/><span></span>
            </div>
          </Command>
          <Command name="delete">
            <div className={styles.com} title="" >
              <IconFont type={`icon-delete`}/><span></span>
            </div>
          </Command>
          <Divider type="vertical" className={styles.divider} />
          <Command name="zoomIn">
            <div className={styles.com} title="" >
              <IconFont type={`icon-zoom-in`}/><span></span>
            </div>
          </Command>
          <Command name="zoomOut">
            <div className={styles.com} title="" >
              <IconFont type={`icon-zoom-out`}/><span></span>
            </div>
          </Command>
        </Toolbar>
        <span className={styles.runningMode} title="">
            <IconFont type={`icon-repeat`}/>
        </span>
      </div>
    )
  }
};

export default FlowToolbar;
